import * as vscode from 'vscode';

import $8kc from './features/completionItemProvider';
import $0kc from './features/hoverProvider';
import $$kc from './features/signatureHelpProvider';
import $dlc from './features/validationProvider';

export function activate(context: vscode.ExtensionContext): any {

	const validator = new $dlc();
	validator.activate(context.subscriptions);

	// add providers
	context.subscriptions.push(vscode.languages.registerCompletionItemProvider('php', new $8kc(), '>', '$'));
	context.subscriptions.push(vscode.languages.registerHoverProvider('php', new $0kc()));
	context.subscriptions.push(vscode.languages.registerSignatureHelpProvider('php', new $$kc(), '(', ','));
}
